.menu-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.4);
  display: flex;
  align-items: flex-end;
  justify-content: center;
  z-index: 1000;
}

.menu-panel {
  background: #fff;
  width: 100%;
  height: 70%;
  max-height: 70%;
  transform: translateY(100%);
  transition: transform 0.3s ease-out;
  box-shadow: 0 -2px 5px rgba(0, 0, 0, 0.3);
  position: relative;
}

.menu-panel.open {
  transform: translateY(0);
}

.tab-buttons {
  display: flex;
  border-bottom: 1px solid #ccc;
}

.tab-buttons button {
  flex: 1;
  padding: 8px;
  background: transparent;
}

.tab-buttons button.active {
  background: #eee;
}

.tab-content {
  padding: 10px;
  overflow-y: auto;
}

.close-button {
  position: absolute;
  top: 5px;
  right: 5px;
  background: transparent;
  font-size: 16px;
}

.map-placeholder {
  text-align: center;
  padding: 20px;
  color: #777;
}
